import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  TablePagination,
} from "@material-ui/core";
import React, { useEffect, useState, forwardRef } from "react";
import MaterialTable from "material-table";
import { MenuItem, Select } from "@material-ui/core";
import axios from "axios";
import AddBox from "@mui/icons-material/AddBox";
import ArrowDownward from "@mui/icons-material/ArrowDownward";
import Check from "@mui/icons-material/Check";
import ChevronLeft from "@mui/icons-material/ChevronLeft";
import ChevronRight from "@mui/icons-material/ChevronRight";
import Clear from "@mui/icons-material/Clear";
import DeleteOutline from "@mui/icons-material/DeleteOutline";
import Edit from "@mui/icons-material/Edit";
import FilterList from "@mui/icons-material/FilterList";
import FirstPage from "@mui/icons-material/FirstPage";
import LastPage from "@mui/icons-material/LastPage";
import Remove from "@mui/icons-material/Remove";
import SaveAlt from "@mui/icons-material/SaveAlt";
import Search from "@mui/icons-material/Search";
import ViewColumn from "@mui/icons-material/ViewColumn";

let rateCardData = [];

export default function BoQPopup(props) {
  const [filteredData, setFilteredData] = useState(rateCardData);
  const [rateCard, setRateCard] = useState("all");
  const { openPopup, setOpenPopup } = props;
  const [selectedRows, setSelectedRows] = useState([]);

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    rateCardData = [];
    axiosInstance
      .get("projonline/allRateCards", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // console.log(res.data)
        const filter = res.data.filter(
          (task) =>
            task.rateCardSubcon._id ===
              JSON.parse(localStorage.getItem("user")).company._id &&
            task.rateCardValidTo >= props.acceptedDate &&
            task.rateCardValidFrom <= props.acceptedDate
        );
        // console.log(filter)
        filter.forEach((card) => {
          card.rateCardItems.forEach((item) => {
            item["rateCardDiv"] = card.rateCardDiv;
            rateCardData.push(item);
          });
        });

        // console.log(rateCardData)
        //setUsers(res.data.users.filter((obj) => (obj["userCanAssignForTask"] === true)))

        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const columns = [
    {
      title: "ERP Code",
      field: "erpCode",
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
    {
      title: "Rate Card Div",
      field: "rateCardDiv",
      cellStyle: { width: 150, minWidth: 150, maxWidth: 150, height: 1 },
    },
    {
      title: "Item Discreption",
      field: "itemDiscreption",
      cellStyle: { width: 400, minWidth: 400, maxWidth: 400, height: 1 },
    },
    {
      title: "UoM",
      field: "itemUom",
      cellStyle: { width: 150, minWidth: 150, maxWidth: 150, height: 1 },
    },
    {
      title: "Unit Rate",
      field: "itemRate",
      type: "currency",
      currencySetting: { currencyCode: "LKR" },
      cellStyle: { width: 200, minWidth: 200, maxWidth: 200, height: 1 },
    },
  ];

  const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => (
      <ChevronRight {...props} ref={ref} />
    )),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => (
      <ChevronLeft {...props} ref={ref} />
    )),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => (
      <ArrowDownward {...props} ref={ref} />
    )),
    ThirdStateCheck: forwardRef((props, ref) => (
      <Remove {...props} ref={ref} />
    )),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
  };

  const handleRowsSelect = () => {
    if (Array.isArray(selectedRows) && selectedRows.length) {
      props.callBk(filteredData.filter((row) => selectedRows.includes(row)));
    }

    setOpenPopup(false);

    const newData = rateCardData.map((row) => ({
      ...row,
      tableData: {
        checked: false,
      },
    }));

    setFilteredData(newData);

    setSelectedRows([]);
  };

  const handleRowsSelectClose = () => {
    setOpenPopup(false);

    const newData = rateCardData.map((row) => ({
      ...row,
      tableData: {
        checked: false,
      },
    }));

    setFilteredData(newData);

    setSelectedRows([]);
  };

  useEffect(() => {
    setFilteredData(
      rateCard === "all"
        ? rateCardData
        : rateCardData.filter((dt) => dt.rateCard === rateCard)
    );
  }, [rateCard]);

  return (
    <Dialog open={openPopup} fullWidth maxWidth="lg">
      <DialogContent>
        <MaterialTable
          title="BOQ Line Items"
          columns={columns}
          data={filteredData}
          icons={tableIcons}
          onSelectionChange={(rows) => setSelectedRows(rows)}
          options={{
            exportButton: true,
            pageSizeOptions: [5, 10, 20],
            paginationType: "stepped",
            showTextRowsSelected: false,
            actionsColumnIndex: -1,
            filtering: true,
            selection: true,
            tableLayout: "auto",
            selectionProps: {},
            rowStyle: {
              backgroundColor: "#EEE",
            },
            headerStyle: {
              backgroundColor: "#01579b",
              color: "#FFF",
            },
          }}
          actions={[
            {
              icon: () => (
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={rateCard}
                  onChange={(e) => setRateCard(e.target.value)}
                  style={{ width: 100 }}
                >
                  <MenuItem value={"all"}>
                    <em>All</em>
                  </MenuItem>
                  <MenuItem value={"TX"}>TX</MenuItem>
                  <MenuItem value={"IBS"}>IBS</MenuItem>
                  <MenuItem value={"BTS"}>BTS</MenuItem>
                </Select>
              ),
              tooltip: "Select Rate Card",
              isFreeAction: true,
            },
          ]}
          components={{
            Pagination: (props) => (
              <div>
                <Grid container style={{ pading: 15, background: "#f5f5f5" }}>
                  <Grid sm={11} align="right" item>
                    <DialogActions>
                      <Button
                        variant="contained"
                        type="submit"
                        color="primary"
                        onClick={handleRowsSelect}
                      >
                        Submit
                      </Button>
                    </DialogActions>
                  </Grid>
                  <Grid sm={1} align="center" item>
                    <DialogActions>
                      <Button
                        variant="contained"
                        color="default"
                        onClick={handleRowsSelectClose}
                      >
                        Close
                      </Button>
                    </DialogActions>
                  </Grid>
                </Grid>
                <TablePagination {...props} />
              </div>
            ),
          }}
        />
      </DialogContent>
    </Dialog>
  );
}
